.reviews {
    display: none;
}

@media( min-width: 768px ){
    
    .reviews {
        display: block;
        max-width: 1220px;
        margin: 30px auto;
    }

    .heading {
        display: -ms-flexbox;
        display: flex;
        position: relative;
        -ms-flex-align: center;
            align-items: center;
        padding: 0 20px;
    }

    .heading .line {
        height: 4px;
        border-bottom: 2px solid var( --red );
        border-radius: 1px;
        -ms-flex-positive: 1;
            flex-grow: 1;
    }

    .heading .title {
        margin: 0 0 0 40px;
        font-size: 1.8em;
        color: var( --red );
        font-family: var( --title-font );
    }

    .listView {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-line-pack: justify;
            align-content: space-between;
        -ms-flex-align: center;
            align-items: center;
        margin: 10px 0;
        width: 100%;
        -webkit-transform-origin: top left;
            -ms-transform-origin: top left;
                transform-origin: top left;
        -webkit-animation: listView .5s;
                animation: listView .5s;
    }

    .listView .preview {
        -ms-flex: 0 0 220px;
            flex: 0 0 220px;
    }
}


