.stats {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-line-pack: distribute;
        align-content: space-around;
}

.stats li {
    display: -ms-flexbox;
    display: flex;
    height: 20px;
    margin: 10px;
}

.stats li span {
    height: 20px;
    width: 20px;
    line-height: 20px;
    font-size: 1em;
    text-align: center;
    vertical-align: middle;
    background-color: var( --light-grey );
    border: 1px solid var( --light-grey );
}

.stats li p {
    margin: 0;
    line-height: 20px;
    vertical-align: middle;
    font-size: .7em;
    padding: 0 6px;
    border: 1px solid var( --light-grey );
}

@media ( min-width: 768px ){
    .isListView .stats li {
        margin: 10px 10px 10px 0;
    }
}


